

import bus from '../bus';
import { Searchbox, Icon } from 'veui';

import 'veui-theme-x/components/Searchbox.js';
import 'veui-theme-x/components/icon.less';
import 'veui-theme-x/components/searchbox.less';
export default {
  name: 'demo-searchbox',
  components: {
    'veui-searchbox': Searchbox,
    Icon: Icon
  },
  data: function data() {
    return {
      name: 'name',
      value: '',
      value2: '',
      placeholder: '(placeholder)',
      suggestions1: [],
      suggestions2: [],
      suggestions3: [],
      suggestions4: [],
      suggestions5: [],
      suggestions6: [],
      suggestions7: []
    };
  },

  methods: {
    handleInput: function handleInput(num, value) {
      console.log(num, value);
      if (value && num < 3) {
        this['suggestions' + num] = [{
          value: value,
          label: value
        }, {
          value: '',
          label: ''
        }, {
          value: '',
          label: ''
        }, {
          value: 'MVP',
          label: 'MVP'
        }];
      } else if (value) {
        this['suggestions' + num] = [value, '', '', 'MVP', ''];
      } else {
        this['suggestions' + num] = null;
      }
    },
    log: function log(item) {
      bus.$emit('log', item);
    }
  }
};


